<template>
  <div class="parent-div" v-loading="loading">
    <el-row><h3></h3></el-row>
    <el-row>
      <el-col :span="8">
        <el-input placeholder="" v-model="searchArticle" style="width: 350px;">
          <i slot="prefix" class="el-input__icon el-icon-search"></i>
        </el-input>
        <el-button size="medium" type="primary" plain><i class="el-icon-search"></i></el-button>
      </el-col>
      <el-col :span="4" :offset="11">
        <el-button size="medium" type="primary" plain @click="addNew"><i class="el-icon-plus"></i></el-button>
        <el-button size="medium" type="primary" plain @click="getArticles"><i class="el-icon-refresh-right"></i></el-button>
        <el-button size="medium" type="primary" plain @click="printMachine"><i class="el-icon-printer"></i></el-button>
      </el-col>
    </el-row>
    <el-row>
      <el-table :data="tableData" style="width: 100%" border>
      <el-table-column sortable label="" prop="id" width="75"></el-table-column>
      <el-table-column label="" prop="title"></el-table-column>
      <el-table-column label="" width="110" prop="author">
        <template slot-scope="scope">
          <el-tag size="medium" disable-transitions>{{scope.row.author}}</el-tag>
        </template>
      </el-table-column>
      <el-table-column sortable label="" prop="editTime" :formatter="dateFormat" width="150"></el-table-column>
      <el-table-column label="" width="110" sortable prop="views">
        <template slot-scope="scope">
          <el-tag size="medium" disable-transitions>{{scope.row.views}}</el-tag>
        </template>
      </el-table-column>
      <el-table-column label="" prop="category" width="140"></el-table-column>
      <el-table-column sortable prop="status" label="" width="100">
        <template slot-scope="scope">
          <el-tag size="medium" :type="scope.row.state === '1' ? 'primary' : 'info'" disable-transitions>{{scope.row.state === '1' ? '':''}}</el-tag>
        </template>
      </el-table-column>
      <el-table-column label="">
        <template slot-scope="scope">
          <el-button type="danger" icon="el-icon-delete" size="mini" @click="deleteOne(scope.$index, scope.row)"></el-button>
          <el-button type="warning" icon="el-icon-edit" size="mini"></el-button>
          <el-button type="primary" icon="el-icon-position" size="mini"></el-button>
        </template>
      </el-table-column>
    </el-table>
    </el-row>
    <el-row>
      <div class="block">
        <el-pagination @size-change="handleSizeChange" @current-change="handleCurrentChange" :current-page="currentPage" :page-sizes="[10, 20, 30, 40]" :page-size="pageSize" layout="total, sizes, prev, pager, next, jumper" :total="totalPage"></el-pagination>
      </div>
    </el-row>
  </div>
</template>

<script>
    import articleApi from "../../api/article";

    export default {
        name: "Article",
      data() {
        return {
          loading: false, //
          searchArticle: '', //
          totalPage: 0, //
          currentPage: 1, //
          pageSize: 10, //
          tableData: [{ //
            id: 1,
            title: '',
            author: '',
            editTime: '',
            views: 0,
            category: '',
            state: ''
          }
          ],

        }
      },
      created(){
          this.loading = true;
          this.getArticles();
      },
      methods: {
        handleSizeChange(val) {
          console.log(` ${val} `);
          this.pageSize = val;
          this.getArticles();
        },
        handleCurrentChange(val) {
          console.log(`: ${val}`);
          this.currentPage = val;
          this.getArticles();
        },
        //
        dateFormat(row, column, cellValue, index){
          return  this.$moment(row.editTime).format("YYYY-MM-DD HH:mm")
        },
        //  
        addNew(){
          this.$router.push('/new')

        },
        //
        getArticles(){
          articleApi.getAll({pageNum: this.currentPage, pageSize: this.pageSize})
            .then(res => {
              this.totalPage = res.data.data.total;
              this.tableData = res.data.data.list;
              this.loading = false;
            })
            .catch(error => {
              this.$message.error("")
            })
        },
        //
        deleteOne(index, row){
          this.$confirm('')
            .then(_ => {
              articleApi.delete(row.id);
              console.log(index, row.id); //rowindex index0
              this.getArticles();
              this.$notify({
                title: '',
                message: '',
                type: 'success'
              });
            })
            .catch(_ => {
              console.log(""+index);
            });
        },
        //
        printMachine(){
          window.print();
        },
      },
    }
</script>

<style scoped>
.parent-div{
  padding: 35px;
  /*border-radius: 5px;*/
  /*box-shadow: 0 0 10px #DCDFE6;*/
  max-width: 1400px;
  margin: 0 auto;
  background-color: #fff;
}
.el-row {
  margin-bottom: 20px;
&:last-child {
   margin-bottom: 0;
 }
}
h3{
  margin: 0 0;
}
</style>



