<template>
  <div class="parent_div">
    <el-row :gutter="20">
      <br>
      <el-col :span="5" :offset="6">
        <el-card class="box-card" v-loading="loading1">
          <div slot="header" class="clearfix">
            <span></span>
            <el-button style="float: right;" type="warning" icon="el-icon-plus" size="medium" circle @click="openAdd(1)"></el-button>
          </div>
          <div v-for="c in categories" :key="c.id" class="text item">
            {{c.name}}
            <el-button style="float: right;" type="primary" size="mini" icon="el-icon-edit" @click="openUpdate(c.id, c.name, 1)"></el-button>
            <el-button style="float: right; margin-right: 8px" type="danger" size="mini" icon="el-icon-delete" @click="deleteSome(c.id, 1)"></el-button>
          </div>
        </el-card>
      </el-col>

      <el-col :span="5" :offset="2  ">
        <el-card class="box-card" v-loading="loading2">
          <div slot="header" class="clearfix">
            <span></span>
            <el-button style="float: right;" type="warning" size="medium" icon="el-icon-plus" circle @click="openAdd(2)"></el-button>
          </div>
          <div v-for="t in tags" :key="t.id" class="text item">
            {{t.name}}
            <el-button style="float: right;" type="primary" size="mini" icon="el-icon-edit" @click="openUpdate(t.id, t.name, 2)"></el-button>
            <el-button style="float: right; margin-right: 8px" type="danger" size="mini" icon="el-icon-delete" @click="deleteSome(t.id, 2)"></el-button>
          </div>
        </el-card>
      </el-col>
    </el-row>

    <el-dialog title="" :visible.sync="deleteOne" width="30%" :before-close="deleteSome">
      <span></span>
      <span slot="footer" class="dialog-footer">
        <el-button @click="deleteOne = false"> </el-button>
        <el-button type="primary" @click="deleteOne= false"> </el-button>
      </span>
    </el-dialog>

  </div>
</template>

<script>
    import categoryApi from "../../api/category";
    import tagApi from "../../api/tag";

    export default {
        name: "Category",
      data(){
        return{
          deleteOne: false, //
          categories: [],
          tags: [],
          loading1: false,
          loading2: false
        }
      },
      created(){
        this.loading1 = true;
        this.loading2 = true;
        this.getCategory();
        this.getTag();
      },
      methods:{
        deleteSome(id, type) {
          let d = type === 1 ? "" : "";
          this.$confirm('')
            .then(_ => {
              if(type === 1){
                this.loading1 = true;
                categoryApi.deleteOne(id);
                this.getCategory();
              }else if(type === 2){
                this.loading2 = true;
                tagApi.deleteOne(id);
                this.getTag();
              }
              this.$message({
                message: '',
                type: 'success'
              });
            });
        },

        openUpdate(id, name, type) {
          this.$prompt('', '', {
            confirmButtonText: '',
            cancelButtonText: '',
            inputValue: name
          }).then(({ value }) => {
            if(type === 1){//
              this.loading1 = true;
              categoryApi.updateOne({id: id, name: value});
              this.getCategory();
            }else if(type === 2){//
              this.loading2 = true;
              tagApi.updateOne({id: id, name: value});
              this.getTag();
            }
            this.$message({type: 'success', message: ': ' + value});
          });
        },

        openAdd(type){
          this.$prompt('', '', {
            confirmButtonText: '',
            cancelButtonText: ''
          }).then(({ value }) => {
            if(type === 1){//
              this.loading1 = true;
              categoryApi.addOne({name: value});
              this.getCategory();
            }else if(type === 2){//
              this.loading2 = true;
              tagApi.addOne({name: value});
              this.getTag();
            }
            this.$message({type: 'success', message: ': ' + value});
          });
        },
        getCategory(){
          categoryApi.getAll().then(res => {
            this.categories = res.data.data;
          });
          this.loading1 = false;
        },
        getTag(){
          tagApi.getAll().then(res => {
            this.tags = res.data.data;
          });
          this.loading2 = false;
        }
      }
    }
</script>

<style scoped>
  .text {
    font-size: 18px;
    text-align: left;
  }

  .item {
    margin-bottom: 18px;
  }

  .clearfix:before,
  .clearfix:after {
    display: table;
    content: "";
  }
  .clearfix:after {
    clear: both
  }

  .box-card {
    width: 400px;
  }
  span{
    font-size: 23px;
    font-weight: bolder;
  }
  .parent-div{
    max-width: 1400px;
    margin: 0 auto;
  }
</style>



